import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import './App.css'
import Home from './panels/Home';
import Persik from './panels/Persik';
import Epic from "@vkontakte/vkui/dist/components/Epic/Epic";
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Posts from "./panels/Posts";
import Сatalog from "./panels/Сatalog";

const App = () => {
	const [activeView, setActiveView] = useState('post');
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [userArr,setUserArr] = useState([])
	const [lang,setLang] = useState('ru')

	useEffect(() => {
		const urlParams = new URLSearchParams(window.location.search);
		setLang(urlParams.get('vk_language' || "ru"))
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
			if (type === 'VKWebAppCallAPIMethodResult') {
				setPopout(null)
				setUserArr(data.response)
			}
		});
		async function fetchData() {
			const user = await connect.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<Epic activeStory={activeView} tabbar={
			<Tabbar>
				<TabbarItem
					onClick={() => setActiveView('post')}
					selected={activeView === 'post'}
					// text="Новости"
				><Icon28Newsfeed /></TabbarItem>
				<TabbarItem
					onClick={() => setActiveView('catalog')}
					selected={activeView === 'catalog'}
					// text="Каталог художников"
				><Icon28More /></TabbarItem>
			</Tabbar>
		}>
			<View activePanel={activePanel} popout={popout} id="post">
				<Posts id='home' fetchedUser={fetchedUser} go={go} />
			</View>
			<View activePanel={activePanel} popout={popout} id="catalog">
				<Сatalog setPopout={setPopout} id='home' fetchedUser={fetchedUser} go={go} userArr={userArr} lang={lang}/>
			</View>
		</Epic>
	);
}

export default App;

